import { Component, OnInit } from '@angular/core';
import {HttpClient} from '@angular/common/http'
import {ToastrService} from 'ngx-toastr'
 var apurl="/api/addproduct"
// const apurl="http://localhost:1000/api/addproduct"
@Component({

  
  selector: 'app-addproduct',
  templateUrl: './addproduct.component.html',
  styleUrls: ['./addproduct.component.css']
})
export class AddproductComponent implements OnInit {
name:null
quantity:null
color:null
price:null
image:null
success:any;
  constructor(private http:HttpClient,private toastr:ToastrService) { }

  ngOnInit() {
  }
addproduct(){
  console.log("Product added");
  var dataobj={
   name:this.name,
   quantity:this.quantity,
color:this.color,
price:this.price,
image:this.image,
success:this.success
  }
  console.log("Data Object ready",dataobj)
  this.http.post(apurl,dataobj).subscribe(
    (response)=>{
      console.log("response from addproduct api:",response)
      this.toastr.success("product added")
    },
 (error)=>{
   console.log("error from product api")
 }
 
    )
}
}
